import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)

const routes = [
  {
    path: '/login',
    component: () => import('@/pages/Login.vue')
  },
  {
    path: '/user',
    component: { render: (c) => c('router-view') },
    children: [
      { path: 'account-summary', component: () => import('@/pages/user/AccountSummary.vue') },
      { path: 'dashboard', component: () => import('@/pages/user/Dashboard.vue') },
      { path: 'direct-referral', component: () => import('@/pages/user/DirectReferral.vue') },
      { path: 'encashment', component: () => import('@/pages/user/Encashment.vue') },
      { path: 'notification', component: () => import('@/pages/user/Notification.vue') },
      { path: 'pairing-bonus', component: () => import('@/pages/user/PairingBonus.vue') },
      { path: 'table-of-exit', component: () => import('@/pages/user/TableOfExit.vue') },
      { path: 'ticket', component: () => import('@/pages/user/Ticket.vue') },
      { path: 'transaction', component: () => import('@/pages/user/Transaction.vue') }
    ]
  },
  {
    path: '/settings',
    component: () => import('@/pages/Settings.vue')
  },
]

export default new  VueRouter({
  mode: 'history',
  routes
})